import React, { Component } from "react";
import "./Modal.css";
class Modal extends Component {
  render() {
    const { id, title, children, showClose = true, noHeader = false, footer } = this.props;
    let { className = "" } = this.props;
    className = className + " modal-dialog";
    return (
      <div className="modal fade"
        id={id}
        tabIndex="-1"
        role="dialog"
        aria-labelledby={title}
        aria-hidden="true"
        data-backdrop="static">
        <div className={className} role="document">
          <div className="modal-content">
            {!noHeader && <div className="modal-header">
              <h5 className="modal-title">{title}</h5>
              {showClose && <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>}
            </div>}
            <div className="modal-body">{children}</div>
            {footer && <div className="modal-footer">{footer}</div>}
          </div>
        </div>
      </div>
    );
  }
}
export default Modal;
