import { ManifestService } from "./services";

export default class Logger {
  static log() {
    console.log(window.extension_name, ...arguments);
  }
  static warn() {
    console.warn(window.extension_name, ...arguments);
  }
  static error() {
    console.error(window.extension_name, ...arguments);
  }
  static info() {
    console.info(window.extension_name, ...arguments);
  }
}

ManifestService.getItem("name").then(name => window.extension_name = name);