import React, { Component } from "react";
import { PlusCircle } from "common/svg";
import { SubscribeContext } from "common/module/subscribe";

const defaultColumns = [{ name: "Name" }, { name: "Init Wait", mode: "PRO", width: "80px" }, { name: "XPath" }, { name: "Value" }, { name: "Repeat", mode: "PRO", width: "100px" }, { name: "R-Interval", mode: "PRO", width: "100px" }];

class ActionHeader extends Component {
  render() {
    const { mode, add, columns } = this.props;
    return <tr>
      <th width="30px"></th>
      {defaultColumns.filter(column => !column.mode || (column.mode === mode && this.context.subscription)).filter(column => {
        let index = columns.findIndex(_column => _column.name.includes(column.name));
        return index === -1 || columns[index].value;
      }).map((column, index) => {
        return <th key={index} width={column.width}>{column.name}</th>
      })}
      <th width="70px"><PlusCircle onClick={add} /></th>
    </tr>;
  }
}
ActionHeader.contextType = SubscribeContext;
export default ActionHeader;