import React, { Component } from "react";
import Anchor from "./Anchor";
import Popover from "./Popover";
import { Input } from "common";
class InputGroup extends Component {

 render() {
  const { id, append, dataTitle, prepend, prependPopover, placeholder, name, type, onChange, onKeyPress, value, disabled, href, index } = this.props;
  let className = `input-group ${this.props.className || ''}`;
  return (
   <div className={className}>
    {!prependPopover && prepend && <div className="input-group-prepend" >
     <span className="input-group-text">{prepend}</span>
    </div>}
    {prependPopover &&
     <Popover className="input-group-prepend" dataTitle={dataTitle || prepend || name} dataContent={prependPopover}>
      {href && <Anchor href={href} className="input-group-text">{prepend || name}</Anchor>}
      {!href && <span className="input-group-text">{prepend || name}</span>}
     </Popover>}
    <Input name={name} id={id} placeholder={placeholder} onChange={onChange} type={type} onKeyPress={onKeyPress} value={value} disabled={disabled} index={index} />
    {append && <div className="input-group-append">
     <span className="input-group-text">{append}</span>
    </div>}
   </div>
  );
 }
}
export default InputGroup;
