import React, { Component } from "react";
import Option from "./Option";
class Select extends Component {
  render() {
    const { id, name, onChange, value, disabled, index } = this.props;
    const className = `custom-select ${this.props.className ? this.props.className : ''}`;
    let options = this.props.options.map((option, index) => <Option key={index} name={option.name} value={option.value} />);
    return <select className={className} name={name} onChange={onChange} value={value} disabled={disabled} index={index} id={id}>
      {options}
    </select>;
  }
}
export default Select;
