import React, { Component } from "react";
import { numberOnlyVal, floatOnlyVal } from "./Validation";
class Input extends Component {
  render() {
    const { id, placeholder, name, onChange, value, disabled, index, type } = this.props;
    let { onKeyPress, className } = this.props;
    if (!onKeyPress && type === "number") {
      onKeyPress = e => numberOnlyVal(e, 10);
    }
    if (!onKeyPress && type === "float") {
      onKeyPress = e => floatOnlyVal(e, 10);
    }
    className = `form-control ${className}`;
    return <input disabled={disabled}
      id={id}
      index={index}
      type="text"
      value={value || ''}
      name={name}
      autoComplete="off"
      className={className}
      placeholder={placeholder}
      onKeyPress={onKeyPress}
      onChange={onChange} />;
  }
}
export default Input;
