import React, { Component } from "react";
import { SubscribeContext, SubscribeLink, SubscribeModal } from "common/module/subscribe";
import SettingsLink from "app/settings/SettingsLink";
import { Anchor, Switch, Popover } from "common";
import { Cog } from "common/svg";
import SettingsModal from "./settings/SettingsModal";

class Header extends Component {

  constructor (props) {
    super(props);
    this.state = { isScroll: false }
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll.bind(this));
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll.bind(this));
  }

  handleScroll() {
    let pageYOffset = window.pageYOffset;
    let isScroll = this.state.isScroll;
    if ((!isScroll && pageYOffset) || (isScroll && !pageYOffset)) {
      this.setState({ isScroll: !isScroll });
    }
  }

  render() {
    const { mode, onChange } = this.props;
    const className = `navbar navbar-expand-md navbar-light bd-navbar flex-column flex-md-row border-bottom ${this.state.isScroll ? 'shadow' : ''}`;
    return <><nav className={className}>
      <a className="navbar-brand" href="./index.html">
        <img src="./icon64.png" width="30" height="30" alt="logo" />
      </a>
      <div className="navbar-nav-scroll">
        <ul className="navbar-nav flex-row w-100">
          <li className="nav-item">
            <Anchor href="#home" className="nav-link active">
              Home
          </Anchor>
          </li>
          <li className="nav-item">
            <Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/getting-started" className="nav-link">
              Documentaion
          </Anchor>
          </li>
          <li className="nav-item">
            <Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/blog" className="nav-link">
              Blog
          </Anchor>
          </li>
          <li className="nav-item">
            <Anchor href="https://dhruv-techapps.github.io/auto-click-auto-fill/examples" className="nav-link">
              Examples
          </Anchor>
          </li>
        </ul>
      </div>
      <ul className="navbar-nav flex-row justify-content-center justify-content-md-end  align-items-center w-100">
        <li className="nav-item mr-3">
          {!this.context.subscription && <Popover dataContent="Get Subscription to enable PRO features" dataTitle="PRO Features" dataPlacement="left" >
            <Switch id="mode" name="mode" checked={mode === "PRO"} onChange={onChange} disabled={true}>PRO</Switch>
          </Popover>}
          {this.context.subscription && <Switch id="mode" name="mode" checked={mode === "PRO"} onChange={onChange}>PRO</Switch>}
        </li>
        <li className="nav-item dropdown">
          <a className="nav-link dropdown-toggle" href="#settings" id="navbarSettings" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            <Cog />
          </a>
          <div className="dropdown-menu dropdown-menu-right" aria-labelledby="navbarSettings">
            <SubscribeLink />
            <SettingsLink />
          </div>
        </li>
      </ul></nav>
      <SubscribeModal />
      <SettingsModal />
    </>;
  }
}
Header.contextType = SubscribeContext;
export default Header;