import React, { Component } from "react";
class Radio extends Component {
 render() {
  const { id, name, value, onChange, checked, disabled } = this.props;
  let className = `custom-control custom-radio ${this.props.className || ''}`;
  return (
   <div className={className}>
    <input className="custom-control-input"
     type="radio"
     disabled={disabled}
     name={name}
     id={id + "_" + name}
     value={value}
     onChange={onChange}
     checked={checked} />
    <label className="custom-control-label" htmlFor={id + "_" + name}>
     {this.props.children}
    </label>
   </div>
  );
 }
}
export default Radio;
