import React, { Component } from "react";
import Config from "app/main/Config";
import Batch from "app/main/Batch";
import Action from "app/main/Action";
import { SubscribeContext } from "common/module/subscribe";

class Main extends Component {

  componentDidUpdate() {
    window.$('[data-toggle="popover"]').popover();
    window.$('[data-toggle="tooltip"]').tooltip();
  }

  render() {
    const { config, onConfigChange, reorderActions, onActionChange, addAction, removeAction, editAddon, mode } = this.props;
    return <main className="col">
      <Config config={config} mode={mode} onChange={onConfigChange} />
      {config && config.enable && <div>
        {mode === "PRO" && this.context.subscription && <Batch config={config} onChange={onConfigChange} />}
        <Action config={config} mode={mode} reorderActions={reorderActions} onChange={onActionChange} add={addAction} remove={removeAction} editAddon={editAddon} />
      </div>}
    </main>;
  }
}
Main.contextType = SubscribeContext;
export default Main;