export const getXPathName = (xpath, index) => {
  return xpath ? `${xpath.split("/").pop().replace(/\[.*\]/, "")}-${index}` : 'name';
}

export const getConfigName = (url, index) => {
  let name = `new-${index}`;
  if (url && url.match("://.*") !== null) {
    name = url.split("/")[2];
  } else if (url) {
    name = url;
  }
  return name;
}