import React, { Component } from "react";
import ActionHeader from "app/main/action/ActionHeader";
import ActionBody from "app/main/action/ActionBody";
import { Anchor, Storage } from "common";
import { ExportService } from "common/services";

const defaultColumns = [{ name: "Name", value: false }, { name: "Init Wait", value: true }, { name: "Repeat & R-Interval", value: true }];
const LOCAL_STORAGE_ACTION_COLUMNS = "action-columns";
class Action extends Component {

  constructor () {
    super();
    this.state = { columns: Storage.getItem(LOCAL_STORAGE_ACTION_COLUMNS, JSON.parse(JSON.stringify(defaultColumns))) };
    this.import = React.createRef();
  }

  onDragOver(e) {
    if (this.dragId !== e.currentTarget.id) {
      e.preventDefault();
    }
  }

  onDragStart(e) {
    this.dragId = e.currentTarget.id;
    e.dataTransfer.setData("dragId", this.dragId);
  }

  onDrop(e) {
    let dragId = e.dataTransfer.getData("dragId");
    if (dragId !== e.currentTarget.id) {
      let dropId = e.currentTarget.id;
      let actions = this.props.config.actions;
      if (dropId >= actions.length) {
        var k = dropId - actions.length + 1;
        while (k--) {
          actions.push(undefined);
        }
      }
      actions.splice(dropId, 0, actions.splice(this.dragId, 1)[0]);
      this.props.reorderActions(actions);
    }
  }


  onColumnChange(e) {
    let { id } = e.currentTarget;
    let columns = this.state.columns.map(column => {
      column.value = column.name === id ? !column.value : column.value;
      return column;
    });
    this.setState({ columns }, () => {
      Storage.setItem(LOCAL_STORAGE_ACTION_COLUMNS, this.state.columns);
    });
  }

  importActions(e) {
    var files = e.currentTarget.files;
    if (files.length <= 0) {
      return false;
    }
    var fr = new FileReader();
    fr.onload = function (e) {
      try {
        var result = JSON.parse(e.target.result);
        if (this.validateConfig(result)) {
          this.props.reorderActions(result);
        } else {
          alert('Invalid Configuration');
        }
      } catch (e) {
        alert('Invalid Configuration');
        console.error(e);
      }
    }.bind(this);
    fr.readAsText(files.item(0));
  }

  validateConfig(json) {
    if (json instanceof Array) {
      if (json.length > 0) {
        return true;
      }
    }
    return false;
  }

  render() {
    const { config, add, remove, onChange, editAddon, mode } = this.props;
    const { actions } = config;
    const { columns } = this.state;
    const onColumnChange = this.onColumnChange.bind(this);
    const onDragOver = this.onDragOver.bind(this);
    const onDragStart = this.onDragStart.bind(this);
    const onDrop = this.onDrop.bind(this);
    const importActions = this.importActions.bind(this);

    return <div id="action" className="card p-3">
      <div className="d-flex align-items-center justify-content-between">
        <Anchor className="mr-4 h4 ml-1 mb-0 font-weight-normal flex-grow-1" href="https://dhruv-techapps.github.io/auto-click-auto-fill/docs/action">Action</Anchor>
        <div className="d-flex">
          <button type="button" className="btn btn-outline-secondary btn-sm mr-2" id="importTrigger" onClick={_ => this.import.current.click()}>Import</button>
          <div className="custom-file d-none">
            <input type="file" className="custom-file-input" ref={this.import} accept=".json" id="import-configuration" onChange={importActions} />
            <label className="custom-file-label" htmlFor="import-configuration" style={{ fontSize: 1 + 'rem', fontWeight: 400 }}>Import</label>
          </div>
          <button type="button" className="btn btn-outline-secondary btn-sm mr-2" id="export-configuration" onClick={_ => ExportService.actions(config)}>Export</button>
          {mode === "PRO" && <>
            <div className="border-right mr-2"></div>
            <div className="dropdown">
              <button className="btn btn-outline-secondary dropdown-toggle btn-sm" type="button" id="dropdownMenuButton" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Columns</button>
              <div className="dropdown-menu dropdown-menu-right" aria-labelledby="dropdownMenuButton">
                {columns.map((column, index) => {
                  let className = `dropdown-item border-bottom ${column.value ? 'active' : ''}`;
                  return <span className={className} style={{ cursor: "pointer" }} id={column.name} value={column.value} key={index} onClick={onColumnChange}>{column.name}</span>
                })}
              </div>
            </div>
          </>}
        </div>
      </div>
      <form className="mt-2">
        <fieldset>
          <table className="table table-hover mb-0 mt-2 table-sm" id="action">
            <thead><ActionHeader mode={mode} columns={columns} add={add} /></thead>
            <tbody>{actions.map((action, index) => <ActionBody columns={columns} mode={mode} action={action} remove={remove} editAddon={editAddon} onDragStart={onDragStart} onDrop={onDrop} onDragOver={onDragOver} onChange={onChange} key={index} index={index} />)}</tbody>
          </table>
        </fieldset>
      </form>
    </div>;
  }
}
export default Action;