import { reject } from "q";

const API_KEY = process.env.REACT_APP_CHROME_EXTENSION_ID;
export default class PaymentService {
 static getPurchases() {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "getPurchases" }, result => {
    result.state === "ERROR" ? reject(result.message) : resolve(result);
   });
  })
 }
 static subscription() {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "subscription" }, resolve);
  });
 }

 static skuDetails() {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "skuDetails" }, result => {
    result.state === "ERROR" ? reject(result.message) : resolve(result);
   });
  });
 }

 static getManualRegistration() {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "getManualRegistration" }, result => {
    result && result.state === "ERROR" ? reject(result.message) : resolve(result);
   });
  });
 }

 static buy() {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "buy" }, result => {
    result.state === "ACTIVE" ? resolve(result) : reject(result.message);
   });
  });
 }

 static consumePurchase(sku) {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "consumePurchase", sku }, result => {
    result.state === "ACTIVE" ? resolve(result) : reject(result.message);
   });
  });
 }
}