import React, { Component } from "react";
class Popover extends Component {
    render() {
        const { dataTitle, dataContent, onClick, index, dataPlacement } = this.props;
        let { className } = this.props;
        return <span className={className} onClick={onClick} index={index} data-placement={dataPlacement} data-title={dataTitle} data-content={dataContent} data-toggle="popover" data-html="true" data-trigger="hover">{this.props.children || dataTitle}</span>;
    }
}
export default Popover;
