import React, { Component } from "react";
import Modal from "common/modal/Modal";
import { Input } from "common";
import Select from "common/Select";

const conditionOptions = [{ name: "~~ Select Condition ~~", value: "" },
{ name: "= Equals", value: "Equals" },
{ name: "!= Not Equals", value: "NotEquals" },
{ name: "~ Contains", value: "Contains" },
{ name: "> Greater Than", value: "GreaterThan" },
{ name: "&lt; Less Than", value: "LessThan" },
{ name: ">= Greater Than Equals", value: "GreaterThanEquals" },
{ name: "&lt;= Less Than Equals", value: "LessThanEquals" }]
class AddonModal extends Component {
 render() {
  const { xpath, value, condition } = this.props.addon;
  const { onChange } = this.props;
  return <Modal id="addon-modal" title="Condition" className="modal-lg">
   <h6>Add condition to be checked before executing Action</h6>
   <hr />
   <div className="container-fluid">
    <div className="row">
     <div className="col">
      <Input name="xpath" value={xpath} placeholder="//element[@id='1']" onChange={onChange} />
     </div>
     <div className="col">
      <Select options={conditionOptions} name="condition" value={condition} onChange={onChange} />
     </div>
     <div className="col">
      <Input name="value" value={value} placeholder="value" onChange={onChange} />
     </div>
    </div>
   </div>
  </Modal>
 }
}
export default AddonModal;