import React, { Component } from 'react';
import Modal from 'common/modal/Modal';
import { Checkbox, Radio, Popover, InputGroup, Storage } from 'common';

const LOCAL_STORAGE_SETTINGS = "settings";
const defaultState = { checkiFrames: false, load: '0', retry: "", retryInterval: "", retryOption: '0', notifications: true }
class SettingsModal extends Component {
  constructor () {
    super();
    this.state = Storage.getItem(LOCAL_STORAGE_SETTINGS, defaultState);
  }

  onChange(e) {
    let { name, id, checked, value, type } = e.currentTarget;
    name = type === "text" ? id : name;
    value = type === "checkbox" ? checked : value;
    this.setState({ [name]: value }, function () {
      Storage.setItem(LOCAL_STORAGE_SETTINGS, this.state);
    });
  }

  render() {
    const { checkiFrames, load, retry, retryInterval, retryOption, notifications } = this.state;
    const onChange = this.onChange.bind(this);
    return <Modal id="settings-modal" title="Settings" className="modal-lg">
      <div>
        <div className="row">
          <div className="col-12 mb-3 mb-md-0 col-md-6">
            {/*-- Check iFrames --*/}
            <Checkbox name="checkiFrames" checked={checkiFrames} onChange={onChange}>
              <Popover dataTitle="Check iFrames" dataContent="Check this box if you want to check xPath within iFrames also" />
            </Checkbox>
          </div>
          <div className="col-12 col-md-6 d-flex flex-column flex-md-row">
            {/*-- On Load --*/}
            <label className="d-inline mr-2">Load Extension With</label>
            <Radio id="onLoadWindow" name="load" value="0" checked={load === '0'} onChange={onChange} className="custom-control-inline">
              <Popover dataTitle="Load With Window" dataContent="Extension is loaded when all content of page is loaded e.g. stylesheets, images, subforms &lt;br/>&lt;span className='text-primary'>* Slower than Document&lt;span>" >Window</Popover>
            </Radio>
            <Radio id="onLoadDocument" name="load" value="1" checked={load === '1'} onChange={onChange} className="custom-control-inline">
              <Popover dataTitle="Load With Document" dataContent="Extension is loaded without waiting for stylesheets, images, and subframes to finish loading &lt;br/>&lt;span className='text-primary'>* Faster than Window&lt;span>">Document</Popover>
            </Radio>
          </div>
        </div>
        <hr />

        {/*-- Retry --*/}
        <div className="row">
          <div className="col-12 mb-2 col-md-6">
            <InputGroup type="number" name="Retry" id="retry" placeholder="5" value={retry} onChange={onChange} prependPopover="Provide specific no of time xpath need to be retry if its not found." />
          </div>
          <div className="col-12 col-md-6">
            {/*-- Retry Interval --*/}
            <div className="input-group">
              <InputGroup type="number" name="Retry Interval" id="retryInterval" placeholder="1" value={retryInterval} onChange={onChange} prependPopover="Provide interval/delay in sec for retry operation to wait before retrying." append="sec" />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <h6 className="my-3 mx-1 text-secondary font-weight-light"><small>* Below are action which can be performed if xpath is not found by extension after retry</small></h6>
          </div>
          <div className="col-12 col-md-3">
            {/*-- Stop --*/}
            <Radio id="stop" value="0" name="retryOption" onChange={onChange} className="custom-control-inline" checked={retryOption === '0'}>
              <Popover dataTitle="Stop" dataContent="Do nothing. Stop extension without procedding further" />
            </Radio>
          </div>
          <div className="col-12 col-md-3">
            {/*-- Skip Not Found --*/}
            <Radio id="skip" value="1" name="retryOption" onChange={onChange} className="custom-control-inline" checked={retryOption === '1'}>
              <Popover dataTitle="Skip Not Found" dataContent="it will skip current action and continue next action">Skip</Popover>
            </Radio>
          </div>
          <div className="col-12 col-md-3">
            {/*-- Check iFrames --*/}
            <Radio id="refresh-page" value="2" name="retryOption" onChange={onChange} className="custom-control-inline" checked={retryOption === '2'}>
              <Popover dataTitle="Retry Refresh" dataContent="it will refresh whole page" />
            </Radio>
          </div>
          <div className="col-12 col-md-3 border-left">
            {/*-- Enable Notification --*/}
            <Checkbox id="notifications" checked={notifications} name="notifications" onChange={onChange} className="custom-control-inline">
              <Popover dataTitle="Show Popup Notifications" dataContent="it will show popup notification with error (e.g. wrong or invalid xpath)" >Show Popup</Popover>
            </Checkbox>
          </div>
        </div>
      </div>
    </Modal>;
  }
}
export default SettingsModal;