import React from 'react';
import ReactDOM from 'react-dom';
import './scss/index.scss';
import App from './App';
import { ManifestService, ErrorService } from 'common/services';
import GAService from 'common/services/GAService';

ManifestService.getItem("version").then(version => {
 window.extension_version = version;
 ReactDOM.render(<App />, document.getElementById('root'));
}).catch(error => {
 console.error(error);
 //window.location.href = "https://dhruv-techapps.github.io/auto-click-auto-fill/"
});

if (process.env.NODE_ENV !== "development") {
 GAService.sendView();
 window.$(document).keydown(function (e) {
  return 123 !== e.keyCode && ((!e.ctrlKey || !e.shiftKey || 73 !== e.keyCode) && void 0)
 });
 window.$(document).on("contextmenu", function (e) {
  e.preventDefault();
 });
 window.onerror = ErrorService.sendMessage;
}

