import React, { Component } from "react";
import SubscribeContext from './SubscribeContext';
class SubscribeLink extends Component {
  render() {
    return <a href="#subscribe" className="dropdown-item" data-toggle="modal" data-target="#subscribe-modal">
      {this.context.subscription && <div>Subscribed</div>}
      {!this.context.subscription && <div>Get Subscription</div>}
    </a>;
  }
}
SubscribeLink.contextType = SubscribeContext;
export default SubscribeLink;