const API_KEY = process.env.REACT_APP_CHROME_EXTENSION_ID;
export default class ManifestService {
 static getItem(key) {
  return new Promise((resolve, reject) => {
   window.browser.runtime.sendMessage(API_KEY, { action: "chrome.manifest", key: key }, function (response) {
    if (window.browser.runtime.lastError) {
     reject(window.browser.runtime.lastError.message);
    } else {
     resolve(response);
    }
   });
  });
 }
}
