import React, { Component } from "react";
class Button extends Component {
 render() {
  const { onClick, disabled, type, index } = this.props;
  let className = `btn ${this.props.className || ''}`;
  return <button disabled={disabled} index={index} className={className} type={type || "button"} onClick={onClick}>{this.props.children}</button>;
 }
}
export default Button;
