const API_KEY = process.env.REACT_APP_CHROME_EXTENSION_ID;
export default class StorageService {
 static setItem(key, value) {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "localStorage", type: "SET", key, value }, resolve);
  })
 }

 static getItem(key, fallback) {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "localStorage", type: "GET", key, fallback }, resolve);
  });
 }

 static removeItem(key) {
  return new Promise(resolve => {
   window.chrome.runtime.sendMessage(API_KEY, { action: "localStorage", type: "REMOVE", key }, resolve);
  });
 }
}
