import React, { Component } from "react";
class Checkbox extends Component {
  render() {
    const { id, name, value, onChange, checked, disabled } = this.props;
    let className = `custom-control custom-checkbox ${this.props.className || ''}`;
    return (
      <div className={className} >
        <input
          disabled={disabled}
          className="custom-control-input"
          type="checkbox"
          name={name}
          id={id + "_" + name}
          onChange={onChange}
          value={value}
          checked={checked}
        />
        <label className="custom-control-label" htmlFor={id + "_" + name} style={{ cursor: "pointer" }}>
          {this.props.children}
        </label>
      </div>
    );
  }
}
export default Checkbox;
